<script setup>
import { ref, onMounted } from 'vue';
import axios from 'axios';
import BigTable from "@/components/BigTable.vue";
import MiniTable from "@/components/MiniTable.vue";
import RouterLink from "@/components/RouterLink.vue"

import { useUserStore } from "@/stores/user.js";
const userData = useUserStore();

// Status Count & Waktu
const workingCount = ref(0);
const smDownCount = ref(0);
const umDownCount = ref(0);
const currentTime = ref('');

// Update jam
function updateClock() {
  const now = new Date();
  const hours = now.getHours().toString().padStart(2, "0");
  const minutes = now.getMinutes().toString().padStart(2, "0");
  const seconds = now.getSeconds().toString().padStart(2, "0");
  currentTime.value = `${hours}:${minutes}:${seconds}`;
}

// Settings Table
const settings = ref({
  title: "Down Equipment",
  size: [10,20,100] //Multiplier, Addition, Minimum
});

const formData = ref({});

const fields = ref({
  firstCol: {label: "TK NO", field: "tk_no", type: "type"},
  priority: ["customer", "alt_location"],
  unpriority: ["username", "createdAt", "lastUpdated"],
  hidden: [],
  toRemove: ["id"],
  exclude: [],
  uneditable: [],
  checkbox: ["spare_exists"],
  date: [],
  datetime: []
});

const urls = ref({
  data: `${userData.apiBaseUrl}/de_site`,
  dropdowns: {
    vehicle_type: {
      url: userData.apiBaseUrl + "/dropdowns/vehicle_type",
      valueField: "vehicle_type",
      column: "vehicle_type"
    },
    tk_no: {
      url: userData.apiBaseUrl + "/dropdowns/tk_no",
      valueField: "tk_no",
      column: "tk_no"
    },
  },
  get: {
    vehicle_type: {
      url: userData.apiBaseUrl + "/get/vehicle_type",
      valueField: "vehicle_type",
      column: "vehicle_type"
    }
  }
});

const fetchTotal = async () => {
try {
  const response = await axios.get(userData.apiBaseUrl + "/get/total_rental");
  workingCount.value = response.data[0];
  smDownCount.value = response.data.length >= 3 ? response.data[1] + response.data[2] : 0;
  umDownCount.value = response.data.length >= 3 ? response.data[2] : 0;
  workingCount.value -= smDownCount.value;
  workingCount.value -= umDownCount.value;
} catch (error) {
  console.error("Fetch error:", error);
}
// console.log("CHECKBOX ", isCheckbox("spare_exists"));
};
// Lifecycle
onMounted(() => {
  // Inisialisasi form kosong
  fetchTotal();
  
  // Mulai jam berjalan
  updateClock();
  setInterval(updateClock, 1000);
});

// Debug log
console.log("User Data");
console.log(userData.level);
console.log(userData.permissions);
</script>

<template>
  <div class="container mt-4">
    <!-- Header -->
    <div class="d-flex justify-content-between align-items-center mb-3">
      <h4 class="fw-bold">TRANSKON - Down Equipment</h4>
      <div class="text-end">
        <h5 class="fw-bold text-primary"></h5>
        <div>{{ currentTime }}</div>
      </div>
    </div>

    <!-- Work Group 
    <div class="mb-3">
      <label class="form-label fw-bold">Work Group</label>
      <select class="form-select w-auto">
        <option>all</option>
        <option>Pama</option>
        <option>THies</option>
        <option>Customer</option>
      </select>
    </div> -->

    <!-- Status Cards -->
    <div class="row text-center mb-4">
      <div class="col">
        <div class="card border-success">
          <div class="card-body">
            <h3 class="text-success">{{ workingCount }}</h3>
            <p class="fw-bold mb-0">On Hire (UIC)</p>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card border-danger">
          <div class="card-body">
            <h3 class="text-danger">{{ smDownCount }}</h3>
            <p class="fw-bold mb-0">Total Unit Down</p>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card border-warning">
          <div class="card-body">
            <h3 class="text-warning">{{ umDownCount }}</h3>
            <p class="fw-bold mb-0">PA Rental</p>
          </div>
        </div>
      </div>
    </div>

    <!-- Equipment Table -->
    <!-- <div class="table-responsive">
      <MiniTable 
          :settings="settings"
          :urls="urls" 
          :fields="fields" 
          :canInsert="false"
          :canUpload="false"
        />
    </div> -->
      <BigTable
        :settings="settings"
        :urls="urls"
        :fields="fields"
        :canInsert="false"
        :canUpdate="false"
        ></BigTable>
    <!-- <div>
      <router-link to="/de/dashboard" class="btn btn-danger mt-3">Back to Dashboard</router-link>
    </div> -->
  
  <div class="mb-4">
    <router-link to="/de/dashboard" class="btn btn-danger mt-3">Go to Dashboard</router-link>
  </div>
</div>
</template>


<style scoped>
.border-success {
  border: 2px solid #198754 !important; /* Bootstrap green */
}
.border-danger {
  border: 2px solid #dc3545 !important; /* Bootstrap red */
}
.border-warning {
  border: 2px solid #ffc107 !important; /* Bootstrap yellow */
}
.card h3 {
  font-size: 28px;
}
</style>

